<template>
  <div>
    <el-dialog
      title="房屋列表"
      :visible.sync="isshow"
      width="50%"
      center="center"
      :before-close="handleClose"
    >
      <el-form ref="form" :model="query" label-width="105px">
        <el-form-item label="房间号：">
          <el-input v-model="query.houseno"></el-input>
        </el-form-item>
        <el-form-item label="小区名称：">
          <el-input v-model="query.buildingid"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="serach">查询</el-button>
        </el-form-item>
      </el-form>
    <!-- 表格 -->
        <Table
        :loading="loading"
        :tableData="tableData"
        :tableCouml="tableCouml"
        @idshow="idshow">
        </Table>
    <!-- 分页 -->
    <Paging :query="query" :total="total" @paging="paging"></Paging>

      <span slot="footer" class="dialog-footer">
        <el-button @click="serct">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";

export default {
  props: ["isshow"],
  data() {
    return {
        loading: true,
        form: {},
        tableData: [],
        tableCouml: [
                { prop: "building", label: "楼号", width: "180"},
                { prop: "houseno", label: "房间号", width: "123"},
                { prop: "", label: "业主", width: "123"},
                { prop: "", label: "电话", width: "164"},
            ],
            query:{
                page:1,
                psize:5
            },
            total:0,
            isde:[]
        };
  },
  components: {
        Table,
        Paging
    },
    created(){
        this.getimet()
        this.$bus.$on('buildingid',id=>{
             this.query.buildingid = Object.values(id).pop()
             this.getimet()
        })
    },
  methods: {
    // 确定
    submit(){
        this.$bus.$emit('isde',this.isde,false)
    },
    idshow(val){
        this.isde.push(val)
    },
    // 查询
    serach(){
        this.getimet()
    },
    // 刷新页面
        paging() {
            this.getimet();
        },
    // 列表
        async getimet(){
            let res = await this.http.get('/houses/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.tableData = res.data.list
                this.loading = false
                this.total = res.data.counts
            }
        },
    // 取消
    serct() {
      // this.isshow = false
      this.$bus.$emit("issho", false);
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(() => {
          // done();
          this.$bus.$emit("issho", false);
        })
        .catch(() => {});
    },
  },
};
</script>

<style lang="less" scoped>
.el-form {
  display: flex;
  /deep/.el-button {
    margin-left: -80px !important;
  }
}
</style>