<template>
    <div>
        <el-card class="box-card">
            <span>缴费管理</span>
        </el-card>
        <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="已缴费" name="first"></el-tab-pane>
            <el-tab-pane label="未缴费" name="second"></el-tab-pane>
        </el-tabs>
        <el-row>
            <el-form ref="form" :model="query" label-width="85px">
                <div class="op">
                    <el-form-item label="姓名：">
                        <el-input v-model="query.name" @change="seratch" clearable @keyup.native.enter="seratch"></el-input>
                    </el-form-item>
                    <el-form-item label="园区名称：">
                       <el-select v-model.trim="query.buildingid" placeholder="请选择" @change="seratch" clearable @keyup.native.enter="seratch">
                        <el-option v-for="imet in this.$store.state.building.buildingdata" :key="imet.id" :label="imet.name" :value="imet.id"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="房号：">
                        <el-input v-model="query.na"></el-input>
                    </el-form-item>
                    <el-form-item label="联系方式：">
                        <el-input v-model="query.mibile" @change="seratch" clearable @keyup.native.enter="seratch"></el-input>
                    </el-form-item>
                    <el-form-item label="账期：">
                        <el-input v-model="query.na"></el-input>
                    </el-form-item>
                    <el-form-item label="添加入：">
                        <el-input v-model="query.admin" @change="seratch" clearable @keyup.native.enter="seratch"></el-input>
                    </el-form-item>
                </div>
                <div class="ol">
                    <el-form-item label="流水单号：">
                        <el-input v-model="query.orderno" @change="seratch" clearable @keyup.native.enter="seratch"></el-input>
                    </el-form-item>
                     <el-form-item>
                        <el-button type="primary" @click="seratch">查询</el-button>
                        <el-button type="primary">重置</el-button>
                    </el-form-item>
                </div>
                </el-form>
        </el-row>
        <el-row class="pl">
            <el-button type="primary" @click="dialogVisible = true">添加</el-button>
            <el-button type="danger" @click="del">批量删除</el-button>
        </el-row>
        
        <!-- 表格 -->
    <Table
      :loading="loading"
      :tableData="tableData"
      :tableCouml="tableCouml"
      @idshow="idshow">
      <template #option="data">
        <el-button size="mini" type="primary">详情</el-button>
        <el-button size="mini" type="success" @click="mod(data.data)">修改</el-button>
        <el-button size="mini" type="danger" @click="handleDelete(data.data)">删除</el-button>
      </template>
    </Table>

    <!-- 分页 -->
    <Paging :query="query" :total="total" @paging="paging"></Paging>

    <!-- 添加 -->
    <Feeadd
    :dialogVisible="dialogVisible"
    @chansgedialogVisible="chansgedialogVisible"
    :modcom="modcom"
    ></Feeadd>
    </div>
</template>
<script>
import Table from "@/components/commts/Table.vue";
import Paging from "@/components/commts/Paging.vue";
import Feeadd from "@/components/fee/feeadd.vue"

export default {
    data(){
        return{
            activeName: 'first',
            loading: true,
            form: {},
            tableData: [],
            tableCouml: [
                { prop: "id", label: "ID", width: "56"},
                { prop: "building", label: "姓名", width: "91"},
                { prop: "building", label: "楼栋号", width: "91"},
                { prop: "houseno", label: "房号", width: "78"},
                { prop: "mibile", label: "联系方式", width: "104"},
                { prop: "stateName", label: "缴费类型", width: "65"},
                { prop: "orientation", label: "账期", width: "78"},
                { prop: "price", label: "应缴金额(元)", width: "78"},
                { prop: "addtime", label: "导入时间", width: "130"},
                { prop: "admin", label: "添加者", width: "91"},
                { prop: "stateName", label: "状态", width: "91"},
                { label: "操作", isshow: true, showname: "option" },
            ],
            total: 0,
            query: {
                page: 1,
                psize: 5,
            },
            ids:[],
            dialogVisible: false,
            modcom:{}
        }
    },
    components: {
        Table,
        Paging,
        Feeadd
    },
    created(){
        this.getimet()
    },
    methods:{
        // 修改
        mod(val){
            this.modcom = JSON.parse(JSON.stringify(val))
            this.dialogVisible = true
        },
        // 查询
        seratch(){
            this.getimet()
        },
        // 批量删除
        del(){
            // console.log(this.ids)
            this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
        })
            .then(async () => {
            let res = await this.http.post("/fee/deleteall", {ids: this.ids,});
            if (res.errCode == 10000) {
                this.$message.success("删除成功");
                this.getimet();
            }
            })
            .catch(() => {
            this.$message({
                type: "info",
                message: "已取消删除",
            });
            });
        },
        idshow(id){
            this.ids.push(id)
        },

        // 删除
        handleDelete(data){
            // console.log(data.id)
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(async () => {
              let res = await this.http.get('/fee/delete',{id:data.id})
              if(res.errCode == '10000'){
                  this.$message.success('删除成功')
                  this.getimet()
              }
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        // 刷新页面
        paging() {
            this.getimet();
        },
        // 列表
        async getimet() {
        let res = await this.http.get("/fee/list", this.query);
        // console.log(res)
            if (res.errCode == "10000") {
                this.tableData = res.data.list;
                this.total = res.data.counts;
                this.loading = false;
            }
        },
        // 添加
        handleClose(done) {
        this.$confirm("确认关闭？")
            .then(() => {
            done();
            })
            .catch(() => {});
        },
        chansgedialogVisible(val) {
            this.dialogVisible = val;
            this.getimet();
        },
    }
}
</script>

<style lang="less" scoped>
@import '@/assets/css/admin.css';
.el-tabs{
    margin-top: 20px;
    margin-bottom: 20px;
}
/deep/.el-tabs--card>.el-tabs__header{
    border: 0px !important;
}
.el-form{
    margin-top: 10px;
    display: block;
    .op{
        display: flex;
    }
    .ol{
        display: flex;
    }
    .el-button{
        padding: 12px 40px;
        margin-right: 20px !important;
    }
}
.pl{
    padding: 10px 40px;
    .el-button{
        padding: 12px 30px;
    }
}

</style>