<template>
<div>
        <el-dialog
    :title="title"
    :visible.sync="dialogVisible"
    width="40%"
    center="center"
    :before-close="handleClose">

    <el-form ref="form" :model="form" label-width="95px">
        <el-form-item label="费用类型：">
            <el-select v-model="form.type" placeholder="请选择">
                <el-option v-for="(imet,index) in jiao" :key="index" :label="imet.name" :value="imet.id"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="金额：" style="width:57%;">
            <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="到期：">
            <div class="ol">
                <el-date-picker type="date" placeholder="开始时间" v-model="form.begindate" style="width: 30%;"></el-date-picker>
                <el-date-picker type="date" placeholder="结束时间" v-model="form.enddate" style="width: 30%;"></el-date-picker>
            </div>
        </el-form-item>
        <el-form-item label="小区：">
            <el-cascader
            v-model="form.buildingid"
            placeholder="请选择"
            :options="this.$store.state.building.buildingdata"
            :props="{label:'name',value:'id',checkStrictly: true }"
            filterable></el-cascader>
        </el-form-item>
        <el-form-item label="缴费房间">
            <el-radio-group>
                <el-radio label="全部"></el-radio>
                <el-radio label="部分房间" @change="fanwu"></el-radio>
            </el-radio-group>
        </el-form-item>
         <el-form-item label="">
            <el-input type="textarea" rows="4" v-model="form.houseids" disabled style="width:60%;"></el-input>
        </el-form-item>
        
    </el-form>

    <span slot="footer" class="dialog-footer">
        <el-button @click="serct">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
    </span>
    </el-dialog>
    <Feewu :isshow="isshow" ></Feewu>
</div>

</template>

<script>
import Feewu from '@/components/fee/feewu.vue'
export default {
    props:{
        dialogVisible:{
            type:Boolean,
            default:false
        },
        modcom:{
            type:Object,
            default:()=>{}
        }
    },
    data(){
        return{
            form:{},
            jiao:[],
            query:{
                page:1,
                psize:5,
            },
            isshow:false,
            ids:[],
            title:'新增缴费'
        }
    },
    components: {
        Feewu
    },
    created(){
        this.getjiao()
        this.dia = this.dialogVisible
        // 取消
        this.$bus.$on('issho',of=>{
            this.isshow = of
        })
        // 确定
        this.$bus.$on('isde',(isde,isshow)=>{
            this.form.houseids = Object.values(isde).pop()
            this.isshow = isshow
        })
    },
    updated(){
        this.title = this.modcom.id?'修改缴费':'新增缴费'
        this.form = this.modcom?this.modcom:{}
        
    },
    methods:{
        // 房屋
        fanwu(){
            this.isshow = true
            this.$bus.$emit('buildingid',this.form.buildingid)
        },
        // 确定
        async submit(){
            this.form.buildingid = this.form.buildingid.join(',')
            let hom = []
            hom.push(this.form.houseids)
            this.form.houseids = hom
            let fpm = {type:this.form.type,
                        begindate:this.form.begindate,
                        enddate:this.form.enddate,
                        price:this.form.price,
                        buildingid:this.form.buildingid,
                        houseids:hom,}
            let res = await this.http.post('/fee/add',fpm)
            console.log(res)
            if(res.errCode == 10000){
                this.$message.success('添加成功')
                this.$emit('chansgedialogVisible',false)
                this.form = {}
            }
            
        },
        // 缴费类型
       async getjiao(){
            let res = await this.http.get('/feetype/list',this.query)
            // console.log(res)
            if(res.errCode == 10000){
                this.jiao = res.data.list
            }
        },
        // 取消
        serct(){
            this.$emit('chansgedialogVisible',false)
        },
        handleClose(done) {
        this.$confirm('确认关闭？')
          .then(() => {
            // done();
            this.$emit('chansgedialogVisible',false)
          })
          .catch(() => {});
      }
    }
}
</script>

<style lang="less" scoped>
.el-form{
    display: block;
}
.el-form-item{
    margin-left: 80px;
    .el-select{
        margin-left: 20px;
    }
    .el-input{
        margin-left: 20px;
    }
    .el-radio{
        margin-left: 20px;
    }
}
/deep/.el-input__inner{
    border: 1px solid #ccc !important;
}
.ol{
    display: flex;
}
</style>